import React from 'react'
import { Segment, List, Label, Item, Image } from 'semantic-ui-react'
import { Link } from 'react-router-dom'
import { observer } from 'mobx-react-lite'
import { Activity } from '../../../app/models/activity'

interface Props{
    activity: Activity;
}

export default observer(function ActivityDetailsSidebar ({activity : {attendees, host}}: Props) {
    if(!attendees) return null;
    const numOfFollowing = attendees.length;
    return (
        <>
            <Segment
                textAlign='center'
                style={{ border: 'none' }}
                attached='top'
                secondary
                inverted
                color='teal'
            >
                {numOfFollowing} {numOfFollowing === 1 ? ' Person Going' : ' People Going'} 
            </Segment>
            <Segment attached>
                <List relaxed divided>
                    {attendees.map((attendee) =>(
                        <Item style={{ position: 'relative' }} key={attendee.userName}>
                            {host?.userName === attendee.userName && (
                                <Label
                                    style={{ position: 'absolute' }}
                                    color='orange'
                                    ribbon='right'
                                >
                                    Host
                                </Label>
                            )}
                            <Image size='tiny' src={attendee.mainPhoto || '/assets/user.png'} />
                            <Item.Content verticalAlign='middle'>
                                <Item.Header as='h3'>
                                    <Link to={`/profiles/${attendee.userName}`}>{attendee.displayName}</Link>
                                </Item.Header>
                                {attendee.isFollowing && (<Item.Extra style={{ color: 'orange' }}>Following</Item.Extra>)}
                            </Item.Content>
                        </Item>
                    ))}
                </List>
            </Segment>
        </>
    )
})